#Place {
    margin: 10px 5px;
    padding: 5px 17px;
    outline: none;
    border-bottom: 2px solid rgb(234, 98, 98);
    background-color: rgb(0, 0, 0);
}

input {
    border: none;
}

#appbtn {
    margin: 10px 5px;
    padding: 4px 16px;
    border: 2px solid rgb(234, 98, 98);
    text-decoration: none;
    border-radius: 5px;
    background-color: rgb(234, 98, 98);
}

#appbtn:hover {
    background-color: rgb(241, 178, 178);
}
